.hairline-remove-right-bottom:after {
  display: none;
}
.hairline-remove-left-top:before {
  display: none;
}
.am-dropdown-wrap {
  position: fixed;
  overflow: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  overflow-scrolling: touch;
  outline: 0;
}
.am-dropdown-mask {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #373737;
  background-color: rgba(55, 55, 55, 0.6);
  height: 100%;
  filter: alpha(opacity=50);
  z-index: 1050;
}
.am-dropdown-mask-hidden {
  display: none;
}
.am-dropdown-close {
  display: none;
}
.am-dropdown-slide-fade-enter,
.am-dropdown-slide-fade-appear {
  transform: translate(0, -100%);
}
.am-dropdown-slide-fade-enter,
.am-dropdown-slide-fade-appear,
.am-dropdown-slide-fade-leave {
  animation-duration: .3s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
  animation-play-state: paused;
}
.am-dropdown-slide-fade-enter.am-dropdown-slide-fade-enter-active,
.am-dropdown-slide-fade-appear.am-dropdown-slide-fade-appear-active {
  animation-name: DropdownSlideFadeIn;
  animation-play-state: running;
}
.am-dropdown-slide-fade-leave.am-dropdown-slide-fade-leave-active {
  animation-name: DropdownSlideFadeOut;
  animation-play-state: running;
}
@keyframes DropdownSlideFadeIn {
  0% {
    transform: translate(0, -100%);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes DropdownSlideFadeOut {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, -100%);
  }
}
.am-dropdown {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  background-color: #fff;
}



